.App {
  text-align: center;
}

.blog-post {
  margin: 20px;
  border: 1px solid #ddd;
  padding: 20px;
}

form {
  margin: 20px;
}

input, textarea {
  width: 100%;
  margin-bottom: 10px;
}

button {
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}
